(function(module) {
try {
  module = angular.module('portal');
} catch (e) {
  module = angular.module('portal', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('templates/blog_detail.html',
    '<section class="container-fluid"><div class="row"><div class="col-xs-12 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2"><div class="post-header"><h1 class="post-title">{{ post.title }}</h1><h3 class="post-subtitle">{{ post.sub_title}}</h3><span class="text-muted">posted by <a href="#/user/{{ post.posted_by }}">{{ post.posted_by }}</a> at {{ post.pub_time }}</span></div><div class="post-content"><div marked="post.body"></div></div></div></div></section>');
}]);
})();
