(function(module) {
try {
  module = angular.module('portal');
} catch (e) {
  module = angular.module('portal', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('templates/landing.html',
    '<header class="container-fluid carousel-wrapper"><div class="row"><div class="col-xs-12 col-carousel"><div id="carousel-landing" class="carousel slide" data-ride="carousel"><ol class="carousel-indicators"><li data-target="#carousel-landing" data-slide-to="0" class="active"></li><li data-target="#carousel-landing" data-slide-to="1"></li><li data-target="#carousel-landing" data-slide-to="2"></li></ol><div class="carousel-inner" role="listbox"><div class="item active" style="background-image: url(\'img/headers/GrayWalking.jpg\')"><div class="carousel-caption caption-right carousel-caption-search"><h2>Which Shoes Match Your Ethics?</h2><form class="form-white"><div class="input-group"><label class="sr-only" for="landing-search">Search</label><input type="text" name="landing-search" id="landing-search" class="form-control" placeholder="Search Shoes, Brands, or References.."><div class="input-group-btn"><button type="submit" value="go" class="btn"><i class="fa fa-fw fa-search"></i></button></div></div></form></div></div><div class="item" style="background-image: url(\'img/headers/GrayShoeHanging.jpg\')"><div class="carousel-caption caption-bottom"><h2 class="heavy">Mindful Click:</h2><h3>Shop With Your Ethics on Amazon or Zappos</h3><button class="btn btn-lg btn-white">Get the Extension</button></div></div><div class="item" style="background-image: url(\'img/headers/GrayWalkingSand.jpg\')"><div class="carousel-caption caption-right caption-squares"><h2 class="caption-title">Browse By Brand</h2><div class="squares-wrapper"><div class="carousel-square"><a href="#/companies/detail/Nike"><img class="img-responsive" src="img/Logos/Nike-mid-white.png"></a></div><div class="carousel-square"><a href="#/companies/detail/Adidas"><img class="img-responsive" src="img/Logos/Adidas-mid-white.png"></a></div><div class="carousel-square"><a href="#/companies/detail/New-Balance"><img class="img-responsive" src="img/Logos/New-Balance-mid-white.png"></a></div><div class="carousel-square"><a href="#/companies/detail/Under-Armour"><img class="img-responsive" src="img/Logos/Under-Armour-mid-white.png"></a></div><div class="carousel-square"><a href="#/companies/detail/Asics"><img class="img-responsive" src="img/Logos/Asics-mid-white.png"></a></div><div class="carousel-square"><a href="#/company/all"><h3>& Many More...</h3></a></div></div></div></div></div><span class="left carousel-control" data-target="#carousel-landing" role="button" data-slide="prev"><i class="fa fa-2x fa-angle-left" aria-hidden="true"></i> <span class="sr-only">Previous</span> </span><span class="right carousel-control" data-target="#carousel-landing" role="button" data-slide="next"><i class="fa fa-2x fa-angle-right" aria-hidden="true"></i> <span class="sr-only">Next</span></span></div></div></div></header><section class="container-fluid" id="recent" ng-controller="RecentCtrl"><div class="row"><div class="col-xs-12 col-md-6 col-lg-4 col-lg-offset-1"><h2 class="title-block title-black">Newly Added Research</h2><ul class="recent-list"><li ng-repeat="tag in tags"><img class="brand-thumbnail" src="img/Logos/{{ tag.company.replace(\' \',\'-\') }}-small-black.png"> <strong><h5 class="recent-header"><a href="{{ tag.link }}">{{ tag.tag_type }}<span ng-show="tag.value">: {{tag.value}}</span></a></h5></strong><span class="text-muted">From: <a href="#/references/detail/{{ tag.reference.id }}">{{ tag.reference }}</a></span></li><li class="text-center recent-button"><a class="btn btn-black" href="#/data/all"><h4>Browse the rest of our ethical data</h4></a></li></ul></div><div class="col-xs-12 col-md-6 col-lg-4 col-lg-offset-2"><h2 class="title-block title-black">Recent/Upcoming Events</h2><ul class="recent-list"><li ng-repeat="event in events"><i class="fa fa-check-circle fa-2x fa-pull-left fa-border"></i> <strong><h5 class="recent-header"><a href="#/events/detail/{{ event.id }}">{{ event.name }}</a></h5></strong><span class="text-muted">{{ event.location.name}}, {{ event.location.city }} -- {{ event.start}}</span></li><li class="text-center recent-button"><a class="btn btn-black" href="#/events/all"><h4>See our full calendar</h4></a></li></ul></div></div><div class="row dark-row"><div class="col-xs-12 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2"><h2 class="title-block title-white">New Blog Posts</h2><ul class="recent-list"><li ng-repeat="post in posts"><strong><h3 class="recent-header recent-header-large"><a href="#/blog/detail/{{ post.title_url }}">{{ post.title.toUpperCase() }}</a></h3></strong><h4 class="recent-subheader">{{ post.sub_title }}</h4><span class="text-muted">posted by <a href="#/users/{{ post.posted_by }}">{{ post.posted_by }}</a> at {{ post.pub_time }}.</span></li><li class="text-center recent-button"><a class="btn btn-white" href="#/blog/all"><h4>Check out the rest of the blog!</h4></a></li></ul></div></div></section><section class="container-fluid"><div class="row"><div class="col-xs-12 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2"><h2 class="title-block title-black">FAQ</h2><uib-accordion close-others="true"><uib-accordion-group is-open="status.open1"><uib-accordion-heading>What is Axiologue?<i class="pull-right fa" ng-class="{\'fa-minus-square-o\': status.open1, \'fa-plus-square-o\': !status.open1}"></i></uib-accordion-heading><p>Axiologue is a crowd-sourced, community-based project to track the ethics of products and companies. We put ethical information in the hands of consumers, so they can shop in line with their own personal ethics and support the companies that are doing it right. We are follow five principles:</p><ul class="fa-ul"><li><i class="fa-li fa fa-usd"></i> <strong>Free</strong>: Ethical information should never be behind a paywall. We believe ethical information should be accessible to anyone who wants access to it.</li><li><i class="fa-li fa fa-user"></i> <strong>Personalized</strong>: Ethics are deeply personal. Rather than assume some universal ethical standard, Axiologue allows you to prioritize what\'s important to you.</li><li><i class="fa-li fa fa-bar-chart"></i> <strong>Comprehensive</strong>: Axiologue covers all possible ethical categories, including environment, labor, political affiliation, and more.</li><li><i class="fa-li fa fa-unlock-alt"></i> <strong>Open</strong>: All of Axiologue\'s data will be publically available, via our own websites/apps and an API so that other developers can access the data directly.</li><li><i class="fa-li fa fa-comments"></i> <strong>Participatory</strong>: Axiologue is a crowd-sourced project, and all you need to contribute is a desire to bring ethical information to those who want it!</li></ul></uib-accordion-group><uib-accordion-group is-open="status.open2"><uib-accordion-heading>Who is Axiologue?<i class="pull-right fa" ng-class="{\'fa-minus-square-o\': status.open2, \'fa-plus-square-o\': !status.open2}"></i></uib-accordion-heading><p>Axiologue is a community-based projects. We are researchers, designers, teachers, artists, philosophers, programmers, students, and more. Axiologue could even be you! All of our data is collected and processed by users like you, so look at our <a href="#/references/how-to">how to contribute</a> guide for information on how to contribute.</p><p>Axiologue was founded by artist/programmer <a href="http://www.nishi-broach.com/">Matt Nishi-Broach</a> and artist/community organizer <a href="http://www.chloebass.com">Chlo Bass</a>.</p></uib-accordion-group><uib-accordion-group is-open="status.open3"><uib-accordion-heading>How Does Scoring Work?<i class="pull-right fa" ng-class="{\'fa-minus-square-o\': status.open3, \'fa-plus-square-o\': !status.open3}"></i></uib-accordion-heading><p>This is such an important topic that we wrote a whole <a href="#/blog/posts/how-unethical-is-a-negative-two">blog post</a> about it.</p><p>The short answer is that we match the facts we know about each company/product with your ethical feelings about each fact. The scores roughly translate to:</p><ul><li>a <strong>postive</strong> score means the product is in line with your ethics!</li><li>a <strong>negative</strong> score means the product is contrary to your ethics!</li><li>a <strong>zero</strong> means you don\'t care about that issue (it could also mean we don\'t have any data in that particular category)!</li></ul><p>To construct your own ethical profile, <a href="#/signUp">sign up</a> for an account. If you don\'t want to make an account at this time, don\'t worry: you can browse our products and see scores for our generic profiles</p></uib-accordion-group><uib-accordion-group is-open="status.open4"><uib-accordion-heading>How can I help?<i class="pull-right fa" ng-class="{\'fa-minus-square-o\': status.open4, \'fa-plus-square-o\': !status.open4}"></i></uib-accordion-heading><p>All our research is collected by user volunteers like you! We wrote a guide to help <a href="#/references/how-to">first-time contributors</a>, and you should drop into our <a href="#/forum">forum</a> if you have have further questions.</p></uib-accordion-group><uib-accordion-group is-open="status.open5"><uib-accordion-heading>Do you only have information on shoes?<i class="pull-right fa" ng-class="{\'fa-minus-square-o\': status.open5, \'fa-plus-square-o\': !status.open5}"></i></uib-accordion-heading><p>As of right now, yes. In fact, we\'re focusing specifically on <em>athletic shoes</em>. We are planning to expand to other product categories in the near future, so if you have suggestions for a certain product category you\'d like us to research, please <a href="MAILTO:info@axiologue.org">email us</a>. We\'d love to hear your suggestions!</p></uib-accordion-group></uib-accordion></div></div></section><section class="container-fluid dark-row"><div class="row"><div class="col-xs-12 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2 text-left"><h2 class="title-block title-white">Stay In Touch</h2><div id="mc_embed_signup"><h3>Sign Up For Our Mailing List</h3><form action="//axiologue.us10.list-manage.com/subscribe/post?u=a4380cffd095d122a97aaeae6&amp;id=1b7d52909b" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" class="validate form-white" target="_blank" novalidate><div id="mc_embed_signup_scroll"><div class="mc-field-group form-group"><label for="mce-EMAIL" class="sr-only">Email Address (required)</label><input type="email" value="" name="EMAIL" class="required email form-control" id="mce-EMAIL" placeholder="Enter Your Email Address (required)"></div><div class="mc-field-group form-group"><label for="mce-FNAME" class="sr-only">First Name</label><input type="text" value="" name="FNAME" class="form-control" id="mce-FNAME" placeholder="Enter First Name"></div><div class="mc-field-group form-group"><label for="mce-LNAME" class="sr-only">Last Name</label><input type="text" value="" name="LNAME" class="form-control" id="mce-LNAME" placeholder="Enter Last Name"></div><div id="mce-responses" class="clear"><div class="response" id="mce-error-response" style="display:none"></div><div class="response" id="mce-success-response" style="display:none"></div></div><div style="position: absolute; left: -5000px" aria-hidden="true"><input type="text" name="b_a4380cffd095d122a97aaeae6_1b7d52909b" tabindex="-1" value=""></div><div class="clear"><input type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe" class="btn btn-white"></div></div></form></div></div></div><div class="row"><div class="col-xs-12 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2 text-center"><hr class="white-rule"></div></div></section>');
}]);
})();
