(function(module) {
try {
  module = angular.module('portal');
} catch (e) {
  module = angular.module('portal', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('templates/auth/passwordreset.html',
    '<section class="container"><div class="row v-center"><div class="col-xs-12 col-sm-offset-2 col-sm-8 col-md-offset-3 col-md-6"><div id="passwordReset_view" ng-controller="PasswordresetCtrl"><h3 class="title-block title-black-filled">Password Reset</h3><div ng-if="complete == false"><form class="form-black" role="form" ng-if="authenticated != true" name="passwordResetForm" ng-submit="resetPassword(passwordResetForm)" novalidate><div class="form-group"><label for="id_email">Email</label><input name="email" id="id_email" type="text" ng-model="model.email" placeholder="Email" class="form-control" required></div><div class="alert alert-danger" ng-repeat="error in errors.email">{{error}}</div><button ng-hide="authenticated" type="submit" class="btn btn-black">Reset Password</button></form><p ng-if="authenticated">You are already logged in! You don\'t need to reset your password.</p></div><div ng-if="complete == true"><div class="alert alert-success">You should receive an email shortly with instructions on how to reset your account password.</div></div></div></div></div></section>');
}]);
})();
